.filter-container {
	margin-bottom: 20px;
	width: 600px;
}

.inputFilter {
	background-color: #f9f9f9;
	border: 1px solid #e0e0e0;
	border-radius: 4px;
	font-size: 14px;
	padding: 8px;
	transition: border-color 0.3s;
	width: 582px;
}

.inputFilter:focus {
	outline: none;
	border-color: #9e9e9e;
}
